.printshots-container
  margin-top: 1.5em

.current-shot-container
  position: relative

.prev-btn
  display: inline
  position: absolute
  left: 2%
  top: 40%
  opacity: 0.5

.next-btn
  display: inline
  position: absolute
  right: 2%
  top: 40%
  opacity: 0.5

.progress-info
  background: $color-bg-dark
